.expenses-filter {
    color: white;
    padding: 0 1rem;
}
.expenses-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
}
.expenses-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
}
.expenses-filter select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
}

.sorting > button {
    background-color: white;
    color: #222222;
    font-weight: 600;
}

.sorting {
    display: flex;
    gap: 10px;
}

.hide-button {
    display: none;
}